              <LevelIcon v-if="showLevel" :level="userInfo.level" />
            </view>
            <view class="tag-row">
              <view class="tag-item" v-for="(item, index) in userInfo.tag_ids_arr" :key="index">
                {{ item.tag_id_str }}
              </view>
            </view>
          </view>
        </view>
        <view class="title">数据统计</view>
        <view class="card-container">
          <view class="static-card">
            <view>志愿服务次数</view>
            <view class="static-num">{{ voluntary_activity_num || 0 }}</view>
            <view class="static-img static">
              <image src="https://cdn-static.papa.com.cn/social/icon-static.png" alt="" />
            </view>
          </view>
          <view class="static-card">
            <view>服务时长（h）</view>
            <view class="static-num">{{ activity_duration || 0 }}</view>
            <view class="static-img num">
              <image src="https://cdn-static.papa.com.cn/social/icon-time.png" alt="" />
            </view>
          </view>
          <view class="static-card">
            <view>服务人数</view>
            <view class="static-num">{{ join_num || 0 }}</view>
            <view class="static-img people">
              <image src="https://cdn-static.papa.com.cn/social/icon-peopleNum.png" alt="" />
            </view>
          </view>
          <view class="static-card">
            <view>服务站点（个）</view>
            <view class="static-num">0</view>
            <view class="static-img location">
              <image src="https://cdn-static.papa.com.cn/social/icon-location.png" alt="" />
            </view>
          </view>
        </view>
      </view>
      <view class="function">
        <view class="title">功能导航</view>
        <view class="card-container">
          <view
            class="card-item"
            v-for="(item, index) in functionList"
            :key="index"
            @click="handleClickItem(item)"
          >
            <view class="function-img">
              <image class="img" :src="item.icon" alt="" />
            </view>
            <view class="fun-title">{{ item.title }}</view>
          </view>
        </view>
      </view>
      <view class="todo-container">
        <view class="title">
          待办服务
          <span class="desc">（0条）</span>
        </view>
        <view class="list-container">
          <!-- <ToDoItem /> -->
          <empty />
        </view>
      </view>
    </view>
  </view>
</template>
<script>
import shareMixins from '@/mixins/shareMixins'
import DefaultAvatar from '@/static/images/avatar.png'
import Medal from '@/static/images/medal.png'
import ToDoItem from './components/ToDoItem.vue'
import LevelIcon from '@/components/LevelIcon/index.vue'
export default {
  mixins: [shareMixins],
  components: {
    ToDoItem,
    LevelIcon,
  },
  data() {
    return {
      navColor: 'transparent',
      titleColor: '#333',
      defaultImg: DefaultAvatar,
      medal: Medal,
      voluntary_activity_num: 0,
      activity_duration: 0,
      join_num: 0,
      tagList: ['太极类', '篮球类'],
      functionList: [
        {
          icon: 'https://cdn-static.papa.com.cn/social/icon-write.png',
          title: '服务风采发布',
          path: '/pages-sub/releaseDynamic/index',
          num: 0,
        },
        {
          icon: 'https://cdn-static.papa.com.cn/social/icon-service.png',
          title: '我的服务',
          path: '/pagesSub/sportsService/serviceAssign/talenList',
          num: 0,
        },
        {
          icon: 'https://cdn-static.papa.com.cn/social/icon-record.png',
          title: '志愿服务记录',
          path: '/pages/tabbar/dynamic/index',
          num: 0,
        },
        {
          icon: 'https://cdn-static.papa.com.cn/social/icon-location2.png',
          title: '我的站点',
          path: '/pages-sub/instructorSite/my',
          num: 0,
        },
      ],
    }
  },
  created() {
    this.getMyVoluntaryData()
  },
  computed: {
    userInfo() {
      return this.$store.user.userInfo
    },
    themeType() {
      return this.$store.app.themeType
    },
    showLevel() {
      return this.userInfo.is_auth_instructor && this.themeType == 'SkyBlue'
    },
  },
  methods: {
    async getMyVoluntaryData() {
      let params = {
        member_id: this.$store.user.userInfo.member_id,
      }
      let res = await this.$api.getMyVoluntaryActivityData(params)
      if (res.code == 200) {
        this.voluntary_activity_num = res.data.voluntary_activity_num
        this.activity_duration = res.data.activity_duration
        this.join_num = res.data.join_num
      } else {
        this.$toast(res.message)
      }
    },
    handleClickItem(item) {
      uni.navigateTo({
        url: item.path,
      })
    },
  },
}
</script>

